import React from "react";
import MainCard from "../Components/MainCard";

export default function SignUpRequest() {
  return (
    <div>
      <MainCard
        title="נתוני ההצבעה שלך שמורים במערכת"
        desc="על מנת נוכל להעביר את הצבעתך לגוף הפנסיה שלך, אנו זקוקים לך כמשתמש רשום"
        text="אני רוצה להרשם"
        tolink="/SignUp"
      />
    </div>
  );
}
